import React from "react";

function OrderModalSizeAndDough(props) {
  const { size, dough, newPizza, handleSizeChange, handleDoughChange } = props;

  return (
    <>
      <h6>Rozmiar:</h6>
      <div className="group" onChange={handleSizeChange}>
        <input
          type="radio"
          name="rb-size"
          id="rb1"
          checked={size === "20cm"}
          value="20cm"
          disabled={newPizza.name === "Calzone (Pierog)"}
          readOnly
        />
        <label htmlFor="rb1">20cm</label>
        <input
          type="radio"
          name="rb-size"
          id="rb2"
          value="28cm"
          checked={size === "28cm" || newPizza.name === "Calzone (Pierog)"}
          readOnly
        />
        <label htmlFor="rb2">28cm</label>
        <input
          type="radio"
          name="rb-size"
          id="rb3"
          value="50cm"
          checked={size === "50cm"}
          disabled={dough === "grube" || newPizza.name === "Calzone (Pierog)"}
          readOnly
        />
        <label htmlFor="rb3">50cm</label>
      </div>
      <h6>Ciasto:</h6>
      <div className="group" onChange={handleDoughChange}>
        <input
          type="radio"
          name="rb-dough"
          id="rb4"
          value="cie켻kie"
          checked={dough === "cie켻kie"}
          disabled={newPizza.name === "Calzone (Pierog)"}
          readOnly
        />
        <label htmlFor="rb4">cie켻kie</label>
        <input
          type="radio"
          name="rb-dough"
          id="rb5"
          value="콑rednie"
          checked={dough === "콑rednie" || newPizza.name === "Calzone (Pierog)"}
          readOnly
        />
        <label htmlFor="rb5">콑rednie</label>
        <input
          type="radio"
          name="rb-dough"
          id="rb6"
          value="grube"
          checked={dough === "grube"}
          disabled={size === "50cm" || newPizza.name === "Calzone (Pierog)"}
          readOnly
        />
        <label htmlFor="rb6">grube</label>
      </div>
    </>
  );
}

export default OrderModalSizeAndDough;
